import React from "react";
import { Link, withRouter } from "react-router-dom";
import styled from "styled-components";
// withRouter로 컴포넌트를 감싸면 history, location, match,staticContext가 props로 들어온다
// withRouter를 사용하면 router에 의해 호출된 컴포넌트가 아니더라도 history, location, match객체에 접근할수있게 해준다

const SHeader = styled.header`
  background-color: rgba(20, 20, 20, 0.8);
  color: #fff;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  display: flex;
  align-items: center;
  padding: 0px 10px;
  z-index: 10;
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.8);
`;

const List = styled.ul`
  display: flex;
  list-style: none;
`;

const Item = styled.li`
  width: 80px;
  height: 50px;
  transition: border-bottom 0.5s ease-in-out;
  border-bottom: 5px solid
    ${(props) => (props.current ? "#3498db" : "transparent")};
`;

const SLink = styled(Link)`
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  color: #fff;
`;
// withRouter로 컴포넌트를 감싸면
const Header = ({ location: { pathname } }) => {
  return (
    <SHeader>
      <List>
        <Item current={pathname === "/" ? true : false}>
          <SLink to="/">Movies</SLink>
        </Item>
        <Item current={pathname === "/tv" ? true : false}>
          <SLink to="/tv">TV</SLink>
        </Item>
        <Item current={pathname === "/search" ? true : false}>
          <SLink to="/search">Search</SLink>
        </Item>
      </List>
    </SHeader>
  );
};

export default withRouter(Header);

