import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import styled from "styled-components";

const Container = styled.div`
  font-size: 12px;
  width: 120px;
  height: 100%;
`;

const Image = styled.div`
  background-image: url(${(props) => props.bgUrl});

  height: 180px;
  background-size: cover;
  border-radius: 4px;
  background-position: center center;
  transition: opacity linear 0.1s;
`;

const Rating = styled.span`
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;

  display: flex;
  justify-content: space-between;

  padding: 0 10px 10px;
  opacity: 0;
  transition: opacity linear 0.1s;
`;

const RatingL = styled.span`
  text-align: justify;
  font-size: 4px;
`;

const Title = styled.span`
  display: block;
  margin-bottom: 3px;
`;

const Year = styled.span`
  font-size: 10px;
  color: rgba(255, 255, 255, 0.5);
`;

const ImageContainer = styled.div`
  position: relative;
  width: 120px;
  height: 180px;
  margin-bottom: 5px;

  background-color: #2c3e50;

  &:hover {
    ${Image} {
      opacity: 0.4;
    }
    ${Rating} {
      opacity: 1;
    }
  }
`;

const Poster = ({ imageUrl, title, id, rating, year, isMovie = false }) => (
  <Link to={isMovie ? `/movie/${id}` : `/show/${id}`}>
    <Container>
      <ImageContainer>
        <Image
          bgUrl={
            imageUrl
              ? `https://image.tmdb.org/t/p/w200${imageUrl}`
              : require("../assets/noPosterSmall.jpg")
          }
        />
        <Rating>
          <RatingL role="img" aria-label="rating">
            {"⭐️".repeat(Math.round(rating / 2))}
            {/* ⭐️ */}
          </RatingL>
          <span>{rating} / 10</span>
        </Rating>
      </ImageContainer>

      <Title>
        {title.length > 18 ? `${title.substring(0, 18)}...` : title}
      </Title>

      <Year>{year}</Year>
    </Container>
  </Link>
);

Poster.propTypes = {
  imageUrl: PropTypes.string,
  title: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  rating: PropTypes.number,
  year: PropTypes.string,
  isMovie: PropTypes.bool,
};

export default Poster;
